import {Line} from 'vue-chartjs'
export default {
  extends: Line,
  props: ['chartData', 'options'],
  mounted () {

    this.renderChart(this.chartData, this.options)
  },
  watch: {
    options () {
      this._chart.destroy();
      this.renderChart(this.chartData, this.options);
    },
    chartData () {
      this._chart.destroy();
      console.log("或得到的监视数据chartData:",this.chartData);
      this.renderChart(this.chartData, this.options);
    }
  }
}



